@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/robotocondensed-regular.woff2") format("woff2"), url("../fonts/robotocondensed-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/robotocondensed-bold.woff2") format("woff2"), url("../fonts/robotocondensed-bold.woff") format("woff");
  font-display: swap;
}
html {
  height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  margin: 0;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  font-family: "PT Mono", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  color: #041827;
  background-color: #f8fdff;
  min-width: 320px;
}

a {
  color: #000000;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

textarea {
  resize: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  /* stylelint-disable-next-line */
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.button {
  display: inline-block;
  box-sizing: border-box;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 18px;
  line-height: 21px;
  font-weight: 700;
  text-align: center;
  color: #f2f5f7;
  border: none;
  text-transform: uppercase;
  cursor: pointer;
  border-radius: 3px;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
  padding-top: 0;
  margin-left: -8px;
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 500px;
  padding: 33px 42px 45px 40px;
  background-color: #00b3ff;
  border-radius: 3px;
}

.modal__wrapper h2 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 30px;
  line-height: 42px;
  font-weight: 400;
  color: #ffffff;
  margin: 0;
  margin-bottom: 8px;
}

.modal__wrapper p {
  margin: 0;
  margin-bottom: 40px;
  color: #ffffff;
}

.modal__form {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.modal__form p {
  margin: 0;
  margin-bottom: 20px;
}

.modal__wrapper textarea {
  margin-bottom: 25px;
}

.modal__wrapper button {
  padding: 14px 60px;
  width: 100%;
}

.modal__wrapper .field__checkbox {
  order: 1;
}

.modal__wrapper span:first-of-type {
  top: 50%;
  left: 8px;
}
.modal__wrapper span:last-of-type {
  margin-left: -5px;
  color: #ffffff;
  letter-spacing: 0.01em;
}

.modal__wrapper .modal__close-btn {
  width: 40px;
  height: 40px;
  border: none;
  background-color: transparent;
  background-image: url("../../img/svg/close-btn.svg");
  background-repeat: no-repeat;
  background-position: center;
  padding: 0;
  position: absolute;
  top: 15px;
  right: 15px;
  cursor: pointer;
  z-index: 1;
}

.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

