  box-shadow: 0px 2px 10px #ffffff;
}

.field input[type=checkbox]:checked + span::before {
  content: "";
  position: absolute;
  top: 3px;
  left: 3px;
  width: 8px;
  height: 5px;
  background-image: url("../../img/svg/check.svg");
  background-repeat: no-repeat;
  background-size: 100%;
}

.field input[type=checkbox] {
  position: absolute;
  opacity: 0;
  left: 7px;
  width: 15px;
  height: 15px;
}

.field__checkbox {
  display: flex;
  justify-content: center;
}

.field__checkbox input:focus + span {
  border-color: #041827;
  background-color: rgba(4, 20, 39, 0.15);
  outline: 5px solid rgba(4, 20, 39, 0.15);
}

